import React from 'react';
import { Route } from "react-router-dom";
import { observer, inject } from "mobx-react";
import ReactPaginate from 'react-paginate';

import '../style/mainPage.css';
import '../style/pagination.css';
import Beta from './pokemonBeta.jsx';
import Pokemon from './pokemon';


let requestRoot = "https://pokeapi.co/api/v2/pokemon/";

window.onload = () => {
    window.location.href = "#/page/1";
}

const Poke = inject('pokemon')(observer(
    class Pokedex extends React.Component {

        componentDidMount() {
            this.props.history.push(`/page/1`);
            let tempArr = JSON.parse(localStorage.getItem('fullArray'));
            if (tempArr !== null) {
                console.log('temparrr', tempArr);
                this.props.pokemon.setFullPokemon(tempArr);
            } else {
                console.log('load');
                // this.props.pokemon.loadFullPokemon().then(response => {
                this.props.pokemon.setFullPokemon(require('../database.json'));
                localStorage.setItem('fullArray', JSON.stringify(this.props.pokemon.fullArray));

                // console.log('the end load');
                // });

            }
        }

        handleSort = () => {
            let tempArray = this.props.pokemon.poketArray;

            let newTemp = JSON.parse(JSON.stringify(tempArray));
            newTemp.sort(function (a, b) {
                var nameA = a[1].toLowerCase(), nameB = b[1].toLowerCase()
                if (nameA < nameB) //сортируем строки по возрастанию
                    return -1
                if (nameA > nameB)
                    return 1
                return 0 // Никакой сортировки
            })
            let tempGrid = [];
            newTemp.map((item) => {
                tempGrid.push(<Pokemon prop={item} key={item[1]} click={this.props.history} />);
                return 0;
            });
            this.props.pokemon.changeArray(tempGrid);
            // console.log(newTemp);
        }

        handleChangeSizeView = (e) => {
            this.props.history.push(`/page/1`);
            this.props.pokemon.changeSelectedPage(0);
            this.props.pokemon.changeSizeView(e.target.value);
            this.props.pokemon.changeAll();

            if (this.props.pokemon.filterPoketArray.length === 0) {
                this.props.pokemon.changePageCount(800 / e.target.value);
                this.getPokemon(1, e.target.value);
            }
            else {
                this.props.pokemon.changePageCount(this.props.pokemon.filterPoketArray.length / e.target.value);
                console.log('handlePageClick');
                let temp = this.props.pokemon.filterPoketArray.filter((element, index) => {
                    if (index + 1 >= 0 && index + 1 <= e.target.value) {
                        return true;
                    }
                    return false;
                });

                temp.forEach((e) => {
                    const request = `${requestRoot}${e[1]}/`;
                    fetch(request, {
                        cache: "force-cache"
                    })
                        .then(response => response.json())

                        .then(data => {
                            const speciesRequest = data.species.url;
                            let temp = [];
                            temp.push(data.sprites['front_default'], data.name, data.stats, data.types, data.weight);
                            this.handlePush(temp);
                            return fetch(speciesRequest);
                        })
                })
            }
        }

        getPokemon = (a, b) => {
            console.log('get pokemon', a, b);
            for (var i = a; i <= b; i++) {
                const request = `${requestRoot}${i}/`;
                fetch(request, {
                    cache: "force-cache"
                })
                    .then(response => response.json())

                    .then(data => {
                        const speciesRequest = data.species.url;
                        let temp = [];
                        temp.push(data.sprites['front_default'], data.name, data.stats, data.types, data.weight);
                        this.handlePush(temp);
                        return fetch(speciesRequest);
                    })
            }
        }

        handlePush = (props) => {
            let tempGrid = [];
            this.props.pokemon.pushPokemon(props);
            tempGrid.push(<Pokemon prop={props} key={props[1]} click={this.props.history} />)
            this.props.pokemon.pushArray(tempGrid);
            localStorage.setItem(JSON.stringify(props[1]), JSON.stringify(props));
        }

        handlePageClick = data => {
            console.log(data);
            let selected = data.selected + 1;
            this.props.history.push(`/page/${selected}`);
            this.props.pokemon.changeAll();
            if (this.props.pokemon.filterPoketArray.length === 0)
                this.getPokemon((this.props.pokemon.sizeView * selected) - this.props.pokemon.sizeView + 1, selected * this.props.pokemon.sizeView);
            else {
                console.log('handlePageClick');
                let temp = this.props.pokemon.filterPoketArray.filter((e, index) => {
                    if (index + 1 >= (this.props.pokemon.sizeView * selected) - this.props.pokemon.sizeView + 1
                        && index + 1 <= selected * this.props.pokemon.sizeView) {
                        return true;
                    }
                    return false;
                });

                temp.forEach((e) => {
                    const request = `${requestRoot}${e[1]}/`;
                    fetch(request, {
                        cache: "force-cache"
                    })
                        .then(response => response.json())

                        .then(data => {
                            const speciesRequest = data.species.url;
                            let temp = [];
                            temp.push(data.sprites['front_default'], data.name, data.stats, data.types, data.weight);
                            this.handlePush(temp);
                            return fetch(speciesRequest);
                        })
                })
            }
            this.props.pokemon.changeSelectedPage(data.selected);
        }

        popUp = () => {
            document.getElementById('popup').className = 'popup--active';
        }

        popUpHide = () => {
            if (document.getElementById('popup').classList.contains("popup--active")) {
                document.getElementById('popup').className = 'popup';
            }
        }

        changeFilter = (props) => {
            this.props.pokemon.changeFilterArray(props);
        }

        setFilter = () => {
            this.props.pokemon.setFiter();
            var flag = false;
            for (let key in this.props.pokemon.filterArray) {
                if (this.props.pokemon.filterArray[key]) {
                    flag = true;
                }
            }
            if (flag === false) {
                this.clearFilter();
            }
            else if (this.props.pokemon.filterPoketArray.length !== 0) {

                this.props.pokemon.changeAll();
                this.props.pokemon.changePageCount(this.props.pokemon.filterPoketArray.length / this.props.pokemon.sizeView);
                for (let i = 0; i < 10; i++) {
                    const request = `${requestRoot}${this.props.pokemon.filterPoketArray[i][1]}/`;
                    fetch(request, {
                        cache: "force-cache"
                    })
                        .then(response => response.json())

                        .then(data => {
                            const speciesRequest = data.species.url;
                            let temp = [];
                            temp.push(data.sprites['front_default'], data.name, data.stats, data.types, data.weight);
                            this.handlePush(temp);
                            return fetch(speciesRequest);
                        })
                }
                this.props.history.push(`/page/1`);
                this.props.pokemon.changeSelectedPage(0);
            }
            if (document.getElementById('popup').classList.contains("popup--active")) {
                document.getElementById('popup').className = 'popup';
            }
        }

        firstFilterLoad = () => {
            console.log('arrayRender', this.props.pokemon.renderFilter);
            let temp = this.props.pokemon.renderFilter.filter((e, index) => {
                console.log('arrayRender', this.props.pokemon.renderFilter);
                if (index + 1 >= 1 && index + 1 <= 10) {
                    return true;
                }
                return false;
            });

            temp.forEach((e) => {
                console.log('setFilter', e);
                this.handlePush(e);
            })
        }

        clearFilter = () => {
            this.props.history.push(`/page/1`);
            this.props.pokemon.changeAll();
            this.props.pokemon.changePageCount(800 / this.props.pokemon.sizeView);
            this.getPokemon(1, this.props.pokemon.sizeView);
            this.props.pokemon.clearFilter();
            this.props.pokemon.changeSelectedPage(0);
        }

        render() {
            // console.log(this.props.pokemon);
            // if (this.props.pokemon.fullArray.length === 800) {
            return (
                <div id='mainPage'>
                    <div className="header">
                        <span>Pokedex</span>
                    </div>
                    <div className='headerButton'>
                        <button className='buttonMenu' onClick={this.handleSort}>sort</button>
                        <select name="sizeView" className='buttonMenu' id="sizeView" size="1" onChange={this.handleChangeSizeView}>
                            <option value="10" >10</option>
                            <option value="20" >20</option>
                            <option value="50" >50</option>
                        </select>
                        <button onClick={this.popUp} id='button' className='buttonMenu'>filter</button>
                        <button onClick={this.clearFilter} id='button' className='buttonMenu'>clear Filter</button>
                        <div className='popup' id='popup'>
                            <div className='bodyPopup'>
                                <div className='headers' ><span onClick={this.popUpHide}>x</span></div>
                                <div className='bodyFilter'>
                                    <ul className='filterContainer'>
                                        {this.props.pokemon.typePokemon.map(key => {
                                            return <li className='filterButton' key={key}>
                                                <span className='filterName'>{key}</span>
                                                <span className={(this.props.pokemon.filterArray[key] === true) ? 'selectFiter--Active' : 'selectFilter'} onClick={() => this.changeFilter(key)}> &#10010; </span>
                                            </li>
                                        })}
                                    </ul>

                                </div>
                                <div className='setFilter'>
                                    <button onClick={this.setFilter}> set filter </button>
                                </div>

                            </div>

                        </div>
                    </div>
                    <div className='pokemonItems'>
                        <Route path="/page/:numberPage" component={Beta} />
                    </div>
                    <div className='alignItem'>
                        <ReactPaginate
                            previousLabel={'<'}
                            nextLabel={'>   '}
                            breakLabel={'...'}
                            breakClassName={'break-me'}
                            pageCount={this.props.pokemon.pageCount}
                            marginPagesDisplayed={2}
                            pageRangeDisplayed={2}
                            onPageChange={this.handlePageClick}
                            containerClassName={'pagination'}
                            subContainerClassName={'pages pagination'}
                            activeClassName={'active'}
                            forcePage={this.props.pokemon.selectedPage}
                        />
                    </div>
                </div>
            );
        }
    }
))

export default Poke;